@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,regular,500,600,700,800,300italic,italic,500italic,600italic,700italic,800italic&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:100,200,300,regular,500,600,700,800,900,100italic,200italic,300italic,italic,500italic,600italic,700italic,800italic,900italic&display=swap);
html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  border: 0;
}

ol[class],
ul[class] {
  list-style: none;
}

a {
  text-decoration: none;
}

button,
a {
  cursor: pointer;
}

img {
  max-width: 100%;
  display: block;
}

button,
input,
select,
textarea {
  color: inherit;
  border: 0;
}

input,
textarea {
  border: none;
  border-radius: 0;
  outline: none;
}

@font-face {
  font-family: "OpenSans";
  src: url("../font/OpenSans-Regular.woff2") format("woff2"), url("../font/OpenSans-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../font/Montserrat-Bold.woff2") format("woff2"), url("../font/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
html,
body {
  height: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1133px;
  width: 100%;
  margin: 0 auto;
  padding: 0 2px;
}

body {
  background-color: #fff;
  font-family: "OpenSans", "Helvetica", "Arial", sans-serif;
  font-size: calc(14px + 2 * (100vw - 340px) / 793);
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
  line-height: 1.6875;
  letter-spacing: normal;
  color: #5a5a5a;
}

.btn {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 16px;
  color: #fffff0;
  align-items: center;
  background-color: #363838;
  border-radius: 21px;
  border: 1px #363838 solid;
  transition: background-color, color, 0.5s ease-out;
  user-select: none;
}
.btn:hover {
  background-color: #fffff0;
  color: #363838;
  color: #363838;
}
.btn:active {
  transform: scale(0.96);
}

.btn-tag {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #8f8d8d;
  align-items: center;
  background-color: transparent;
  border-radius: 21px;
  border: 1px solid #8f8d8d;
  transition: background-color, color, 0.5s ease-out;
  user-select: none;
}
.btn-tag:hover {
  background-color: #ffef35;
  color: #363838;
  border-color: #ffef35;
}
.btn-tag:active {
  transform: scale(0.96);
}

.play {
  width: 80px;
  height: 80px;
  background-color: #979797;
  border-radius: 50px;
  transition: all 0.3s;
}
.play__go {
  width: 0;
  height: 0;
  border-top: 18px solid transparent;
  border-bottom: 18px solid transparent;
  border-left: 28px solid rgba(255, 255, 255, 0.9294117647);
  margin: 22px auto;
  display: block;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.8509803922);
}
.play:active {
  transform: scale(0.96);
}

.input {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #a1a1a1;
  align-items: left;
  border: 1px solid #dfdfdf;
  border-radius: 20px;
  transition: background-color, color, 0.5s ease-out;
}
.input:hover {
  color: #363838;
}
.input:focus {
  border-color: #363838;
  color: #363838;
}

.title {
  color: #363838;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 110px;
  line-height: 134px;
  letter-spacing: 0px;
}

.sub-title {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 60px;
  line-height: 80px;
  letter-spacing: 0px;
  color: #363838;
}

.block-title {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 51px;
  letter-spacing: 0px;
  color: #363838;
}